/* -----------------------------------------
Default style
-------------------------------------------- */
html {
  font-size: 16px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: url(pics/HogwartsBG-1.jpg) top no-repeat;
  background-size: cover;
  background-attachment: fixed;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 100;
}
/* every tag */
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
}

:root {
  --primary-color: #caa75b;
}

.header-img {
  width: 90%;
  max-width: 720px;
}
